import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {MessagesProvider} from "../../providers/messages/messages";

/**
 * Generated class for the UserSettingsPushNotificationPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-user-settings-push-notification',
  templateUrl: 'user-settings-push-notification.html',
})
export class UserSettingsPushNotificationPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, private messages: MessagesProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad UserSettingsPushNotificationPage');
  }

  private pushNotif() {
    setTimeout(() => {
      this.messages.sendTestNotification();
    }, 5500)
  }

}



